$width: 350px;
$height: 150px;
$containerBackgroundColour: #f7f7f7;

.container {
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $containerBackgroundColour;

  &__image {
    display: flex;
    position: relative;
    width: $width;
    height: $height;
    background-size: cover;
    margin: 65px auto 30px auto;
    align-items: center;
    background-image: url("../Images/logo.png");
  }
}
